import React from 'react';
import './Player.css';
import song from '../../assets/music/cutthis.mp3';

class Player extends React.Component {

  initProgressBar = this.initProgressBar.bind(this);
  calculateTotalValue = this.calculateTotalValue.bind(this);
  calculateCurrentValue = this.calculateCurrentValue.bind(this);
  initPlayers = this.initPlayers.bind(this);
  mute = this.mute.bind(this);
  updateVolume = this.updateVolume.bind(this);

  initProgressBar() {
    let player = document.getElementById('player'),
        length = player.duration,
        current_time = player.currentTime;

        // calculate total length of value
    let totalLength = this.calculateTotalValue(length)
    document.getElementById("end-time").innerHTML = totalLength;

    // calculate current value time
    let currentTime = this.calculateCurrentValue(current_time);
    document.getElementById("start-time").innerHTML = currentTime;

    let progressbar = document.getElementById('seek-bar');
    progressbar.value = (player.currentTime / player.duration);
    progressbar.addEventListener("click", seek);

    if (player.currentTime === player.duration) {
      document.getElementById('play-btn').className = "";
    }

    function seek(event) {
      let percent = event.offsetX / this.offsetWidth;
      player.currentTime = percent * player.duration;
      progressbar.value = percent / 100;
    }
  }

  updateVolume(event) {
    let audioElement = document.getElementById('player'),
        volume = document.getElementById('volume'),
        xPosition = event.pageX - event.target.offsetLeft,
        clickedValue = xPosition * event.target.max / event.target.offsetWidth;

    clickedValue = (clickedValue > 1) ? 1 : clickedValue;
    clickedValue = (clickedValue < 0) ? 0 : clickedValue;

    console.log("clickedValue " + clickedValue + " audioElement " + audioElement)
    volume.value = clickedValue;
    audioElement.volume = clickedValue;
  }

  initPlayers(num) {
    console.log("init " + num)
    for (let i = 0; i < num; i++) {
      (function() {
        // audio embed object
        let playerContainer = document.getElementById('player-wrap'),
          player = document.getElementById('player'),
          isPlaying = false,
          playBtn = document.getElementById('play-btn');

        console.log("player " + player + " playBtn " + playBtn)
        // Controls Listeners
        if (playBtn != null) {
          console.log("playBtn " + playBtn)
          playBtn.addEventListener('click', function() {
            togglePlay()
          });
        }

        // Controls & Sounds Methods
        function togglePlay() {
          if (player.paused === false) {
            player.pause();
            isPlaying = false;
            document.getElementById('play-btn').className = "";

          } else {
            player.play();
            document.getElementById('play-btn').className = "pause";
            isPlaying = true;
          }
        }
      }());
    }
  }

  calculateTotalValue(length) {
    let minutes = Math.floor(length / 60),
      seconds_int = length - minutes * 60,
      seconds_str = seconds_int.toString(),
      seconds = seconds_str.substr(0, 2),
      time = minutes + ':' + seconds

    return time;
  }

  calculateCurrentValue(currentTime) {
    let current_hour = parseInt(currentTime / 3600) % 24,
      current_minute = parseInt(currentTime / 60) % 60,
      current_seconds_long = currentTime % 60,
      current_seconds = current_seconds_long.toFixed(),
      current_time = (current_minute < 10 ? "0" + current_minute : current_minute) + ":" + (current_seconds < 10 ? "0" + current_seconds : current_seconds);

    return current_time;
  }

  mute() {
    let player = document.getElementById('player'),
        speaker = document.getElementById('mute');
    player.muted ? player.muted = false : player.muted = true;
    speaker.classList.toggle("muted");

  }

  componentDidMount() {
    this.initPlayers(document.getElementsByClassName("audio-wrapper").length);
  }

  render() {
    return (
      <div className="audio-player">
        <div id="play-btn"></div>
        <div className="audio-wrapper" id="player-wrap" href="">
          <audio id="player" onTimeUpdate={this.initProgressBar} src={song}></audio>
        </div>
        <div className="player-controls scrubber">
          <div className="title">Cut This</div>
          <div id="seek-wrap">
            <progress id="seek-bar" value="0" max="1"></progress>
          </div>
          <div className="time-wrap">
            <small id="start-time">0:00</small>
            <small id="end-time">0:00</small>
          </div>
          <progress id="volume" className="volume custom-progress" min="0" max="1" step="0.01" value="1" onClick={this.updateVolume}></progress>
        </div>
        <div className="volume-wrap">
          <a href="#" id="mute" className="speaker" onClick={this.mute}>
            <span className="speakerSpan"></span>
          </a>
        </div>
      </div>
    );
  }
}

export default Player;
